.list {
  &__item {
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid $border-color;
  }
}

.list-2 {
  &__item {
    &:not(:last-child) {
      border-bottom: 1px dashed $border-color;
      padding-bottom: 15px;
      margin-bottom: 15px;
    }
  }
}
