.logo {
  display: inline-flex;
  margin: 14px 0;

  img {
    height: 42px;
  }
}

.header {
  position: relative;
  z-index: 100;
  background-color: $white;

  &__inner {
    display: flex;
    align-items: center;
    position: relative;
  }

  &__elements {
    display: flex;
    align-items: center;
    margin-left: auto;
  }

  &__btn {
    display: flex;
    flex-direction: column;
    width: 24px;
    height: 24px;
    justify-content: center;
    align-items: center;
    position: relative;

    &:not(:last-child) {
      margin-right: 16px;
    }

    span {
      display: none;
    }

    img {
      height: 21px;
    }
  }

  &__btn-number {
    display: flex;
    width: 16px;
    height: 16px;
    justify-content: center;
    align-items: center;
    color: $white;
    font-size: 11px;
    background-color: $danger;
    border-radius: 50%;
    position: absolute;
    top: -3px;
    left: 50%;
  }

  @media (min-width: 1200px) {
    &__btn {
      width: auto;
      height: auto;

      span {
        display: block;
        font-size: 12px;
        text-transform: uppercase;
        color: $primary;
        white-space: nowrap;
        margin-top: 4px;
      }

      &:not(:last-child) {
        margin-right: 30px;
      }
    }
  }
}

.navigation {
  padding: 0 0 8px;

  &__inner {
    display: flex;
    align-items: center;
    position: relative;
  }

  @media (min-width: 1200px) {
    padding: 0;
  }
}

.lang {
  position: relative;
  z-index: 1;

  &__toggle {
    display: inline-flex;
    height: 35px;
    align-items: center;
    background-color: $white;
    color: #333;
    padding: 0 5px;
    border-radius: 4px;

    &:hover {
      color: #333;
      text-decoration: none;
    }

    img {
      height: 22px;
    }
  }

  &__dropdown {
    display: none;
    min-width: 160px;
    border-radius: 4px;
    padding: 4px 0;
    position: absolute;
    z-index: 10;
    top: 100%;
    right: -15px;
    margin-top: 8px;
    background-color: $white;
    @include shadow("xs");

    &::before,
    &::after {
      content: "";
      border: 5px solid transparent;
      border-bottom-color: #999;
      position: absolute;
      bottom: 100%;
      right: 40px;
    }

    &::after {
      margin-top: 1px;
      border-bottom-color: $white;
    }
  }

  &__item {
    display: flex;
    height: 40px;
    align-items: center;
    padding: 4px 20px;
    color: #333;

    &:hover {
      color: #333;
      text-decoration: none;
      background-color: $light;
    }

    img {
      height: 16px;
      margin-right: 16px;
    }

    &:not(:last-child) {
      border-bottom: 1px dashed $border-color;
    }
  }
}

.search {
  flex-grow: 1;

  @media (min-width: 1200px) {
    position: absolute;
    top: -35px;
    left: 170px;
    right: 380px;
    transform: translateY(-50%);

    .form-control {
      height: 42px;
      border: 2px solid $primary;
      padding-left: 14px;
    }
  }
}
