.follow {
  background-color: $primary;
  padding: 18px 0;

  &__inner {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__label {
    display: flex;
    align-items: center;
    font-size: 16px;
    color: $white;
    text-transform: uppercase;
    margin-right: 26px;

    img {
      height: 32px;
      margin-right: 24px;
    }
  }

  &__form {
    flex-grow: 1;
    max-width: 635px;
    border: 3px solid $white;
    border-radius: 4px;
    background-color: $white;

    .form-control {
      padding-left: 12px;
      border: 0;
      height: 38px;
    }

    .input-group-append {
      background: 0;
    }

    .input-group-text {
      font-size: 14px;
      font-weight: 700;
      color: $white;
      background: #3f9dd6;
      padding: 0 30px;
      border: 0;
      border-radius: 4px !important;
    }
  }

  @media (max-width: 767px) {
    &__inner {
      flex-direction: column;
      align-items: center;
    }

    &__label {
      margin-right: 0;
      margin-bottom: 18px;

      img {
        height: 24px;
        margin-right: 20px;
      }
    }

    &__form {
      width: 100%;
    }
  }
}

.fs-title {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  color: $white;

  img {
    height: 25px;
    width: 25px;
    object-fit: cover;
    object-position: 50% 50%;
    border-radius: 50%;
    margin-right: 10px;
  }
}

.countdown {
  display: flex;
  align-items: center;
  margin-left: 10px;
  padding-left: 10px;
  border-left: 2px solid $white;
  color: $white;

  &__timer {
    display: flex;

    span {
      display: inline-flex;
      width: 30px;
      height: 24px;
      background-color: $white;
      border-radius: 4px;
      justify-content: center;
      align-items: center;
      color: #333;
      font-size: 14px;
      margin-left: 10px;

      & + span {
        margin-left: 15px;
        position: relative;

        &::before {
          content: ":";
          display: flex;
          width: 15px;
          height: 100%;
          justify-content: center;
          align-items: center;
          color: $white;
          position: absolute;
          top: 50%;
          left: -15px;
          transform: translateY(-50%);
        }
      }
    }
  }

  @media (max-width: 575px) {
    flex: 0 0 100%;
    margin-left: 0;
    padding-left: 0;
    border-left: 0;
    margin-top: 8px;
    justify-content: flex-end;
  }
}

.flash-sale-tabs {
  background-color: $white;
  padding: 8px 20px;
}

.flash-timer {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 10px;
  background-color: $white;
  padding: 10px;
  margin-bottom: 20px;

  &__item {
    display: block;
    text-align: center;
    color: #333;

    &:hover {
      color: #333;
      text-decoration: none;
    }

    &.active {
      color: $danger;

      &:hover {
        color: $danger;
      }
    }
  }

  &__time {
    font-size: 20px;
    font-weight: 700;
  }

  @media (max-width: 575px) {
    &__time {
      font-size: 16px;
    }

    &__label {
      font-size: 12px;
      line-height: 1.2;
    }
  }
}

.link {
  color: $secondary;
  text-decoration: underline;

  &:hover {
    color: $secondary;
  }
}
