.md-success {
  &__content {
    background: url("../images/modal-bg.jpg") center/cover no-repeat;
    border-radius: 20px;
    padding: 50px 30px;
    font-size: 14px;
    line-height: 24px;
  }

  @media (min-width: 576px) {
    .modal-dialog {
      max-width: 600px;
    }
  }
}

.md-cart {
  &__content {
    border-radius: 20px;
    padding: 30px;
  }

  &__btns {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 30px;

    .button {
      text-transform: uppercase;
      min-width: 205px;

      & + .button {
        margin-left: 30px;
      }
    }
  }

  @media (min-width: 576px) {
    .modal-dialog {
      max-width: 600px;
    }
  }

  @media (max-width: 575px) {
    &__content {
      padding: 30px 20px;
    }

    &__btns {
      flex-direction: column;

      .button + .button {
        margin-left: 0;
        margin-top: 10px;
      }
    }
  }
}
